export default {
	props: {
		active: { // 当前是哪一个页面
			type: Number,
			default: 0
		}
	},
	data() {
		return {}
	},
	methods:{
		skipTo(flag){
			if(flag == this.active){
				return false;
			}
			switch (flag){
				case 0:
					uni.navigateTo({
						url: '../../pages/index/index'
					});
					break;
				case 1:
					uni.navigateTo({
						url: '../../pages/product/product'
					});
					break;
				case 2:
					uni.navigateTo({
						url: '../../pages/shopping/shopping'
					});
					break;
				case 3:
					uni.navigateTo({
						url: '../../pages/user/user'
					});
					break;
				default:
					break;
			}
		}
	}
}
